<template>
	<view class="content">
		<uni-list>
			<uni-list-item title="任务编号" :rightText="detail.id"></uni-list-item>
			<uni-list-item title="创建时间" :rightText="detail.createdTime"></uni-list-item>
			<uni-list-item title="任务名称" :rightText="detail.taskName"></uni-list-item>
			<uni-list-item title="任务类型" :rightText="taskTypeOptions[detail.taskType]"></uni-list-item>
			<uni-list-item title="巡检内容" :rightText="detail.content"></uni-list-item>
			<template v-if="roleId == '4' || roleId == '8'">
				<uni-list-item title="责任中队" :rightText="detail.jurisdiction"></uni-list-item>
			</template>
			<template v-if="roleId == '5' || roleId == '8'">
				<uni-list-item title="巡检片区" :rightText="detail.checkArea"></uni-list-item>
				<uni-list-item title="网格警员" :rightText="detail.gridPolice" v-if="roleId != '8'"></uni-list-item>
			</template>
			<uni-list-item title="预计完成时间" :rightText="detail.preCompleteDate"></uni-list-item>
			<uni-list-item title="实际完成时间" :rightText="detail.completeTime" v-if="finish"></uni-list-item>
			<uni-list-item title="隐患排查结果" rightText="查看详情" link v-if="finish&&roleId=='8'" @click="checkTroubleDetail"></uni-list-item>
		</uni-list>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			roleId: uni.getStorageSync('roleId'),
			detail: {},
			id: '',
			taskTypeOptions: {},
			finish: false
		};
	},
	onLoad(options) {
		this.id = options.id;
		this.finish = options.finish;
		this.getTaskTypeOptions();
		if (this.id) {
			this.getTaskDetail(this.id);
		}
	},
	methods: {
		getTaskTypeOptions() {
			//获取任务类型
			taskAPI.getSysDict({ type: 'task_type' }).then(res => {
				if (res.code == 200) {
					let obj = {};
					res.data.map(item => {
						obj[item.value] = item.label;
					});
					this.taskTypeOptions = obj;
				}
			});
		},
		getTaskDetail(id) {
			taskAPI.getTaskDetail({ id }).then(res => {
				if (res.code == 200) {
					this.detail = res.data;
				}
			});
		},
		checkTroubleDetail() {
			//跳转至隐患详情列表页面
			
			let userId = uni.getStorageSync('username');
			console.log(userId)
			uni.navigateTo({
				url: `../patrolTask/troubleList?userId=${userId}&id=${this.detail.id}&finish=true`
			});
		}
	}
};
</script>

<style></style>
